/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  background-color: #E5E5E5;
}

.wrapper {
  max-width: 1440px;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}
.wrapper img {
  max-width: 100%;
}
@media screen and (min-width: 890px) {
  .wrapper img {
    width: 505px;
    height: 330px;
    margin-right: 95px;
    margin-top: 96px;
  }
}
@media screen and (min-width: 890px) {
  .wrapper {
    display: flex;
    flex-direction: row;
    max-width: 1440px;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
  }
}

.Beach__Desc h1 {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 120%;
  margin-bottom: 24px;
  margin-top: 20px;
}
@media screen and (min-width: 890px) {
  .Beach__Desc h1 {
    width: 380px;
    height: 50px;
    margin-top: 136px;
  }
}
.Beach__Desc p {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  margin-bottom: 48px;
}
@media screen and (min-width: 890px) {
  .Beach__Desc p {
    width: 380px;
    height: 96px;
  }
}
.Beach__Desc button {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 16px 40px;
  gap: 10px;
  border-radius: 32px;
  background: #E4C1AE;
  font-size: 16px;
  line-height: 16px;
  margin: 0 auto;
  color: #FFFFFF;
  cursor: pointer;
  border: none;
}
@media screen and (min-width: 890px) {
  .Beach__Desc button {
    width: 128px;
    height: 48px;
    margin: 0;
  }
}